.trans-no {
	transition: none;
}
.trans-no-all {
	* {
		transition: none;
		&:before,
		&:after {
			transition: none;
		}
	}
}
ul.no-list {
	margin: 0;
	padding: 0;
	list-style: none;
	ul,
	ol,
	li {
		margin: 0;
		padding: 0;
		list-style: none;
	}
}
ol.no-list {
	margin: 0;
	padding: 0;
	list-style: none;
	ol,
	ul,
	li {
		margin: 0;
		padding: 0;
		list-style: none;
	}
}
.flex {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-wrap: wrap;
}
.fit-cover {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	object-fit: cover;
}
.no-select {
	user-select: none;
}
.no-select-all {
	* {
		user-select: none;
	}
}
.overlay {
	display: block;
	position: absolute;
	content: '';
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
}
.address-style {
	ul {
		@extend .no-list;
		li {
			display: flex;
			margin-bottom: 10px;
			color: $color-primary-200;
			&:last-child {
				margin-bottom: 0;
			}
			em {
				display: flex;
				justify-content: center;
				width: 20px;
				margin-right: 10px;
			}
			span {
				flex: 1;
				a {
					color: $color-primary-200;
					&:hover {
						color: $color-gray-700;
					}
				}
			}
		}
	}
}
.firago {
	font-family: var(--font-family-primary);
	font-weight: normal;
	font-feature-settings: 'case' on;
	text-transform: uppercase;
}
